html {
  --color-active: #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}

header {
  justify-content: space-between;
  align-items: center;
  padding-inline: 50px;
  display: flex;
}

.nav__list {
  text-transform: uppercase;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  align-items: center;
  height: 60px;
  margin: 0;
  padding: 0;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--color-active);
}

.nav__item {
  align-items: center;
}

.is-active:after {
  content: "";
  background-color: var(--color-active);
  border-radius: 5px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
